import { NextRequest, NextResponse } from "next/server";
import { prisma } from "@/lib/prisma";

const OLLAMA_BASE_URL =
  process.env.OLLAMA_BASE_URL?.replace(/\/$/, "") ?? "http://localhost:11434";
const OLLAMA_VISION_MODEL = process.env.OLLAMA_VISION_MODEL ?? process.env.OLLAMA_MODEL ?? "llama3.2-vision";

function getId(request: NextRequest, context: { params?: { id?: string } }) {
  const direct = context?.params?.id;
  if (direct) return direct;

  const pathname = new URL(request.url).pathname;
  const parts = pathname.split("/").filter(Boolean);
  return parts[parts.length - 2] || null;
}

function extractBase64(dataUrl: string) {
  const match = dataUrl.match(/^data:.*?;base64,(.*)$/);
  return match ? match[1] : null;
}

export async function POST(request: NextRequest, context: { params?: { id?: string } }) {
  const id = getId(request, context);
  if (!id) {
    return NextResponse.json({ error: "Missing palette id." }, { status: 400 });
  }

  const body = await request.json().catch(() => null);
  const dataUrl = typeof body?.imageBase64 === "string" ? body.imageBase64 : null;
  if (!dataUrl) {
    return NextResponse.json({ error: "imageBase64 is required." }, { status: 400 });
  }

  const base64 = extractBase64(dataUrl);
  if (!base64) {
    return NextResponse.json({ error: "Invalid image data." }, { status: 400 });
  }

  const palette = await prisma.palette.findUnique({ where: { id } });
  if (!palette) {
    return NextResponse.json({ error: "Palette not found." }, { status: 404 });
  }

  const payload = {
    model: OLLAMA_VISION_MODEL,
    stream: false,
    format: "json",
    messages: [
      {
        role: "system",
        content:
          "You are a color extraction assistant. Return ONLY valid JSON with a single key 'colors' as an array of 4-8 hex colors (e.g. #AABBCC).",
      },
      {
        role: "user",
        content: "Extract the dominant palette colors from this image.",
        images: [base64],
      },
    ],
  };

  let resultText = "";
  try {
    const res = await fetch(`${OLLAMA_BASE_URL}/api/chat`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(payload),
    });

    if (!res.ok) {
      const err = await res.text();
      return NextResponse.json(
        { error: "Ollama request failed.", detail: err },
        { status: 500 }
      );
    }

    const data = await res.json();
    resultText = data?.message?.content ?? data?.response ?? "";
    if (!resultText) {
      return NextResponse.json(
        { error: "Empty response from Ollama.", detail: JSON.stringify(data) },
        { status: 500 }
      );
    }
  } catch (error) {
    return NextResponse.json(
      {
        error: "Failed to reach Ollama. Make sure it is running.",
        detail: String(error),
      },
      { status: 500 }
    );
  }

  let parsed: { colors?: unknown } | null = null;
  try {
    parsed = JSON.parse(resultText.trim());
  } catch {
    parsed = null;
  }

  const colors = Array.isArray(parsed?.colors)
    ? parsed?.colors.filter((color: unknown) => typeof color === "string")
    : [];

  if (colors.length === 0) {
    return NextResponse.json(
      { error: "No colors returned from Ollama.", detail: resultText },
      { status: 500 }
    );
  }

  const merged = Array.from(new Set([...(palette.colors ?? []), ...colors]));
  const updated = await prisma.palette.update({
    where: { id },
    data: { colors: merged },
  });

  return NextResponse.json({ palette: updated, added: colors });
}
